.main-page .header-extension{
    background-color: #B73F3F;
    padding-left: 100px;
    padding-right: 100px;

    margin-bottom: 40px;

    height: 600px;
}

.main-page .header-extension .divider{
    height: 2px;
    width: 228px;
    margin-left: 10px;
    background-color: black;
}

.main-page .header-extension .title{
    font-family: Five Years Later Demo;
    font-style: normal;
    font-weight: normal;
    font-size: 96px;
    line-height: 90px;
    letter-spacing: -0.05em;

    text-align: start;

    margin-bottom: 20px;
}

.main-page .header-extension .text-flex{
    display: flex;
    flex-direction: column;
    align-items: start;

    padding-top: 90px;

    width: 450px;
}

.main-page .header-extension .aux-text{
    text-align: start;
    margin-top: 40px;
    font-size: 20px;
}


.main-page .news{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.main-page .H1{
    margin-left: 10%;
    margin-top: 60px;
}

.main-page .headings{
    margin-top: 40px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-left: 9%;
    padding-right: 9%;
}

.main-page .carousel-flex{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}