.search-container{
    display: flex;
    flex-direction: column;

    width: 450px;
    height: 470px;
    margin: 20px;
    padding: 40px 30px 30px;

    box-shadow: 5px 6px 40px rgba(0, 0, 0, 0.25);
    border-radius: 15px;

    align-content: left;
    background-color: #E6D8D8;
}
.search-container input{
    height: 40px;

    background: #FFFFFF;
    border: 1px solid #888888;
    border-radius: 10px;
    font-size: 18px;
    line-height: 21px;
    padding-left: 20px;
    padding-right: 20px;
    margin-bottom: 20px;
}

.search-container .H2{
    margin-top: 8px;
    margin-bottom: 8px;
}

.search-container .react-datepicker-wrapper input{
    width: 135px;
}

.search-container .find{
    width: 147px;
    height: 58px;
    margin-left: 260px;

    background: #B63D3D;
    border-radius: 10px;
    color: white;

    font-family: Helvetica;
    font-style: normal;
    font-weight: bold;
    font-size: 22px;
    line-height: 36px;
}

input{
    outline: none;
}

button{
    outline: none;
}