.internships-page .header-extension{
    background-color: #B73F3F;
    padding-top: 10px;
    padding-bottom: 20px;
}

.internships-page .header-extension .H1{
    color: black;
}

.internships-page .H1{
    margin-left: 10%;
    margin-top: 60px;
    margin-bottom: 30px;

    color: white;
}

.internships-page .internships{
    display: flex;

    flex-wrap: wrap;

    margin-left: 10px;

    margin-top: 40px;
    margin-bottom: 60px;
}

.internships-page .intern-ship{
    color: white;
    margin-bottom: 40px;
}

.internships-page .intern-ship .H2{
    color: white;
}

.internships-page{
    background-color: black;
}

.internships-page .footer-container{
    background-color: black;

}

.internships-page .footer-container .H2{
    color: white;
}

.internships-page .header-extension button{
    display: flex;
    margin: 10px;
    height: 60px;
    width: 250px;
    justify-content: space-between;
    border-radius: 10px;

    font-size: 20px;
    padding-left: 20px;
    padding-right: 20px;
    background: white;
}

.internships-page .header-extension .bubble{
    position: absolute;
    margin: 10px;
    width: 210px;
    padding: 20px;
    border-radius: 10px;
    background-color: white;
    z-index: 1000;
}

.internships-page .header-extension .bubble button{
    background: none;
    border: none;
    font-size: 20px;
    margin: 0;
    width: 210px;

    text-align: left;
}

.internships-page .header-extension .buttons{
    display: flex;
    margin-left: 10%;
}

.internships-page .header-extension input{
    width: 170px;
    height: 40px;
    border-radius: 10px;

    padding-left: 20px;
    padding-right: 20px;
    font-size: 20px;
}