.intern-ship{
    position: relative;
    height: 530px;
    width: 350px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: center;
}

.intern-ship img{
    height: 400px;
    width: 300px;
    object-fit: cover;
    border-radius: 10px;
    align-self: center;
}

.intern-ship .image-container{
    height: 400px;
    width: 300px;
}

.intern-ship div{
    text-align: left;
    padding-left: 25px;
    padding-right: 25px;
    padding-top: 5px;
}

.intern-ship .description{
    max-height: 3.5em;

    overflow: hidden;
    padding-top: 0px;

    font-size: 20px;
}

.intern-ship .H2{
    font-weight: bold;

    word-wrap: break-word;

    font-size: 30px;
}

.intern-ship .container{
    padding-left: 0;
}

.intern-ship .speciality{

    font-size: 20px;
}

.intern-ship button{
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    opacity: 0;
}