.footer-container{
    display: flex;
    justify-content: space-around;
    padding-bottom: 50px;
}

.footer-container button{
    background: none;
    outline: none;
    border: none;
    height: 40px;
    text-align: left;
}

.footer-container a{
    background: none;
    outline: none;
    border: none;
    height: 40px;
    text-align: left;
    padding-left: 7px;
}

.footer-container .container{
    display: flex;
    flex-direction: column;
    align-items: start;
}

.footer-container .sub-buttons{
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    margin-top: 20px;
}

.footer-container .T0{
    height: 25px;
    font-size: 18px;
    color: #979797;
    line-height: 1em;
}