.header-container{
    background: #B73F3F;
    height: 80px;
    display: flex;
    justify-content: space-around;
}

.header-container .buttons{
    align-self: center;
    display: flex;
    background-color: black;
    justify-content: space-between;
    padding-right: 40px;
    padding-left: 40px;

    width: 85%;
    height: 45px;
}

.header-container .buttons .H2{
    background: none;
    text-decoration: none;
    color: white;
    outline: none;
    border: none;
    height: 30px;
}

.header-container .buttons .container{
    margin-top: 6px;
}

.header-container .buttons .container.extended{
    height: 100px;
}

.header-container .bubble{
    position: absolute;
    width: 150px;

    top: 50px;
    left: -50px;

    background: rgba(235, 233, 233, 0.9);
    box-shadow: 0px 4px 40px rgba(0, 0, 0, 0.25);
    border-radius: 10px;

    z-index: 1000;
}

.header-container .container{
    position: relative;
}

.header-container .bubble{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: left;

    padding: 40px;
}

.header-container .bubble .divider{
    width: calc(100% - 40px);
    height: 3px;
    background-color: black;
}

.header-container .bubble button{
    margin-top: 5px;
    margin-bottom: 5px;

    background: none;
    border: none;
    outline: none;

    font-size: 20px;

    text-align: left;
}