.news-box{
    position: relative;
    padding: 40px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    margin: 20px;
}

.news-box.big{
    width: 700px;
    height: 500px;
}


.news-box.medium{
    width: 500px;
    height: 600px;
}


.news-box.small{
    width: 300px;
    height: 500px;
}


.news-box img{
    max-height: 40%;
    object-fit: cover;
}

.news-box .publication-date{
    text-align: left;
    margin-bottom: 15px;
}

.news-box .text{
    overflow: hidden;
    justify-content: space-between;
    line-height: 1.2em;
    margin-top: 15px;

    text-align: left;
}

.news-box .H15{
    margin-top: 20px;
    margin-bottom: 20px;

}

.news-box.small .H15{
    margin-top: 20px;
    margin-bottom: 10px;

    font-size: 40px;
}

.news-box.medium .H15{
    margin-top: 20px;
    margin-bottom: 21px;
}

.news-box.small .text{
    max-height: 9.6em;
}

.news-box .fade{
    height: 30px;
    width: calc(100% - 80px);
    position: absolute;

    bottom: 40px;
    /*background-attachment: fixed;*/
}

.news-box button{
     position: absolute;
     top:0;
     left: 0;
     width: 100%;
     height: 100%;
     z-index: 100;
     opacity: 0;
 }
