.book-container{
    width: 400px;
    height: 650px;
    overflow: hidden;
    position: relative;

    margin: 20px;
}

.book-container .page{
    position: absolute;
    overflow: hidden;
    width: 365px;
    height: 568px;

    background: #FFFFFF;
    box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
}

.book-container .page.cover.front{
    transition: width 1s;

    left: 0;
    top: 0;
}

.book-container .page.cover.front.open{
    width: 0;
}

.book-container .page.front{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
}

.book-container .quote-text{
    position: relative;
    width: 264px;
    height: 65px;
    margin-top: 140px;

    font-family: Helvetica;
    font-style: normal;
    font-weight: normal;
    font-size: 48px;
    line-height: 48px;
    text-align: center;

    color: #FFF7F7;

}

.book-container .quote{
    position: absolute;
    width: 282px;
    height: 266px;
    left: 71px;
    top: 157px;

    font-family: Helvetica;
    font-style: normal;
    font-weight: normal;
    font-size: 28px;
    line-height: 32px;

    color: #000000;
}

.book-container .quote-name{
    position: relative;
    width: 148px;
    height: 36px;
    margin-bottom: 40px;

    font-family: Helvetica;
    font-style: normal;
    font-weight: normal;
    font-size: 28px;
    line-height: 32px;

    color: #FFFFFF;
}

.book-container .quotes{
    position: absolute;
    width: 97px;
    height: 125px;

    font-family: Arapey;
    font-style: normal;
    font-weight: normal;
    font-size: 288px;
    line-height: 316px;

    color: #BD0000;
}

.book-container .tongue{
    position: absolute;
    width: 36px;
    height: 78px;
    top: 546px;
    left: 273px;

    background: #C4C4C4;
    box-shadow: 6px 6px 40px rgba(0, 0, 0, 0.25);
    border-radius: 60px;
    transform: matrix(-1, 0, 0, 1, 0, 0);
}

.book-container button{
    position: absolute;
    width: 365px;
    height: 568px;
    left: 0;
    top: 0;
    outline: none;

    border: none;

    background-color: rgba(255, 255, 255, 0);
}

.book-container .page.cover{
    left: 27px;
    top: 13px;

    background: #B73F3F;
}

.book-container .page.middle{
    left: 21px;
    top: 8px;
}

.book-container .page.quote-container{
    left: 12px;
    top: 3px;
}

