.news-container{
    padding: 40px;
    display: flex;
    justify-content: space-between;

    background-color: #EDEBEB;
    width: 80%;
    height: 420px;
    margin: auto;
    box-shadow: -6px -6px 40px rgba(0, 0, 0, 0.3);
}

.news-container.inverse{
    background-color: #4D4D4D;
}

.news-container.inverse .H15{
    color: white;
}

.news-container.inverse .text{
    color: white;
}

.news-container img{
    object-fit: cover;

    align-self: center;
    width: 50%;
    height: 100%
}

.news-container .text-container{
    width: 45%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.news-container .text-container .date-container{
    display: flex;
    justify-content: space-between;
}

.news-container .text-container .H15{
    margin-left: 0px;
    width: 90%;

    max-height: 2em;
    overflow: hidden;
}

.news-container .text-container .text{
    max-height: 14em;
    text-align: left;
    margin-left: 10px;
    overflow: hidden;
}

.news-container .text-container .date-container .date{
    font-size: 14px;
    line-height: 16px;
    letter-spacing: -0.05em;

    color: #C03D3D;
}

.news-container .text-container .date-container .tag{
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    letter-spacing: -0.05em;

    color: #C03D3D;

}

.news-container button{
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    opacity: 0;
}