.slick-slider {
    margin: 32px 0px 64px;
    position: relative;

    overflow: hidden;
}

.slick-slide div{
    outline: none;
}

.slick-slider .slick-list {
    width: 80%;

    margin-left: 10%;

    outline: none;
}

.slick-slider .slick-list .slick-track {
    position: relative;
    top: 0;
    left: 0;

    display: block;
    margin-left: auto;
    margin-right: auto;
    height: auto;
}

.slick-slider .slick-list .slick-track .slick-slide {
    float: left;
}

.slick-slide .carousel-flex{
    height: 600px;
}

.slick-slide.slick-current{
    opacity: 1 !important;
}