.intern-page .header-container{
    padding-bottom: 60px;

    background-color: black;
}

.intern-page{
    background-color: black;

    height: 100vh;
}

.intern-page .header-container{
    background-color: black;
}

.intern-page .H1{
    color: white;
    margin-left: 10%;
    margin-bottom: 60px;
}

.intern-page .img-flex{
    display: flex;

    justify-content: space-around;
}

.intern-page img{
    height: 340px;
    width: 270px;
    object-fit: cover;
    align-self: center;
}

.intern-page .text-col{
    display: flex;
    width: 270px;
    flex-direction: column;
    align-items: left;

    color: white;
}

.intern-page .text-col .title{
    font-weight: bold;

    margin-top: 30px;

    font-size: 25px;
    text-align: left;
}

.intern-page .text-col .text{

    margin-top: 10px;
    margin-left: 2px;

    font-size: 20px;

    text-align: left;
}